@import 'variables';
@import 'select2';
@import 'yesno';

.rpp-admin {
	padding: 0;
	margin: 0 1rem 0 0;

	.bordered-box {
		background-color: #fff;
		padding: 1rem;
		border: 1px solid $rev-green;
		border-radius: $radius-outer;
	}

	.header {
		width: 100%;
		position: fixed;
		z-index: 999;

		.header-content {
			width: 100%;
			max-width: 1080px;
			height: $header-height;
			place-items: stretch;
			display: grid;
			align-items: center;
			grid-template-columns: 1fr auto;
			gap: 1rem 1rem;
			padding: $header-v-padding 0;
			margin: 0;

			img {
				height: 39pt;
				padding: 0;
				margin: 0 ;
				border: 0 none;
			}

			a {
				color: #555;
				font-size: 18pt;
				text-decoration: none;
				margin: 0 0 0 33pt;

				&.active {
					color: $rev-green;
					text-decoration: underline;
				}
			}
		}
	}

	.notices-list {
		padding: $header-full-height 0 0 0;
		margin: 0 0 1rem 0;

		.notice {
			margin-left: 0;
		}
	}

	.main {
		max-width: 1080px;
		place-items: stretch;
		display: grid;
		grid-template-columns: 1fr 300px;
		gap: 1rem 1rem;
		padding: 0;
		margin: 0;

		.sidebar {
			padding: 0;
			margin: 0;

			.info {
				font-size: 0.91rem;

				.bordered-box {
					display: block;
					margin: 0 0 1rem 0
				}

				a {
					color: $rev-green;
				}

				.anchor {
					font-size: 0.91rem;
					font-weight: 700;
					text-align: center;
					text-decoration: none;

					img {
						width: 60%;
					}
				}

				h2, h3, h4 {
					color: $rev-green;
					text-align: center;
					line-height: 1.59rem;
				}

				.join {
					background-color: $rev-green;
					color: #fff;
					font-size: 1.17rem;
					font-weight: 700;
					text-align: center;
					text-decoration: none;
					line-height: 1.57rem;
					cursor: pointer;
					display: block;
					outline: 0;
					padding: 0.31rem 0.71rem;
					margin: 1.27rem 0 0 0;
					border: 0 none;
					border-radius: 6px;
					box-shadow: rgba(27, 31, 35, 0.04) 0px 1px 0px 0px, rgba(255, 255, 255, 0.25) 0px 1px 0px 0px inset;
					transition: 0.1s cubic-bezier(0.3, 0, 0.5, 1);

					&:hover {
						background-color: #006e0c;
						transition-duration: 0.22s;
					}
				}

				ul {
					text-align: left;
					padding: 0;
					margin: 0;

					li {
						list-style-type: disc;
						margin: 0 0 0.6rem 1.21rem;

						&:first-child {
							list-style-type: none;
							font-weight: 500;
							text-align: center;
							margin: 0 0 0.6rem 0;
						}
					}
				}
			}
		}
	}
}

@import 'page_settings';
@import 'page_guide';
