/** Styled checkbox **/

input.yesno {
	display: none;

	& + .decor {
		color: #333;
		font-size: 1.5em;
		font-weight: normal;
		text-align: center;
		cursor: pointer;
		display: inline-block;
		vertical-align: middle;
		min-width: 0.99em;
		opacity: 0.4;

		&:before {
			color: #333;
			content: "\25cb";
		}
	}

	&:checked + .decor {
		opacity: 1.0;

		&:before {
			color: $rev-green;
			content: "\29bf";
		}
	}

	&:disabled {
		opacity: 0.2;
	}
}
