.settings-options {
	display: flex;
	height: 100%;
	align-items: end;

	.save-button {
		background-color: $rev-green;
		color: #fff;
		font-size: 13pt;
		font-weight: 500;
		width: 9rem;
		height: 100%;
		padding: 0.23rem 1.23rem;
		margin: 0 0 0 1rem;
		border: 1px solid $rev-green;
		border-radius: $radius-inner;

		&:hover {
			background-color: #fff;
			color: $rev-green;
		}
	}
}

.rpp-admin-snippet {
	padding: 0;
	margin: 0 0 2rem 0;

	.title {
		background-color: $rev-green;
		color: #fff;
		font-size: 1.27rem;
		font-weight: bold;
		padding: 10px;
		border: 1px solid $rev-green;
		border-radius: $radius-inner $radius-inner 0 0;

		&.drag {
			border-radius: $radius-inner;
		}

		.priority {
			display: none;
			float: right;

			.move {
				color: #fff;
				cursor: grab;
			}
		}
	}

	.body {
		padding: 10px;
		border: 1px solid $rev-green;
		border-radius: 0 0 $radius-inner $radius-inner;
	}

	.snippet-actions {
		display: flex;
		justify-content: space-between;
		margin: 1em 0 0 0;

		a, button {
			color: #fff;
			text-decoration: none;
			padding: 0.51rem 2.11rem;
			border: 0 none;
			border-radius: $radius-inner;

			&:hover {
				text-decoration: none;
			}

			&.preview {
				background: $rev-green;
			}

			&.delete {
				background: #f22;
			}
		}
	}
}

.rpp-form {
	place-items: stretch;
	display: grid;
	grid-template-columns: minmax(140px, 30%) 1fr;
	gap: 0.5rem;
	padding: 0;
	margin: 0;

	.label-line{
		grid-column: 1 / span 2;
	}

	input.user,
	textarea.user{
		width: 100%;
	}

	.option {
		margin-bottom: 0.77rem;

		.two-links {
			display: flex;
			justify-content: space-between;
		}
	}

	.select-box {
		display: none;
		width: 100%;
		padding: 0.45rem 0 0.22rem 2.11rem;

		&.active {
			display: block;
		}
	}

	.rule-multi-select {
		width: 100%;
	}

	li.select2-selection__choice {
		margin-bottom: 0;
	}

	li.select2-search {
		input[type="search"] {
			line-height: 1.0em;
			min-height: auto;
		}
	}

	.snippet-submit {
		font-size: 1rem;
	}
}

.rpp-code-box {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 1fr auto;
	gap: 15px;
	width: 100%;
	height: calc(100% - 10px);
	padding: 0;
	margin: 10px auto 0 auto;

	.code {
		grid-column: 1 / span 2;
	}

	button {
		background: #fff;
		color: $rev-green;
		text-decoration: none;
		padding: 0.51rem 2.11rem;
		border: 1px solid $rev-green;
		border-radius: $radius-inner;

		&:hover {
			text-decoration: none;
		}

		&.main {
			background: $rev-green;
			color: #fff;
		}
	}
}

.rpp-code-wait {
	display: none;

	p {
		font-size: 1.9rem;
		text-align: center;
	}
}

.rpp-params-wait {
	p {
		font-size: 1.9rem;
		text-align: center;
	}
}
