.rpp-guide {
	font-size: 0.91rem;
	padding: 1.21rem;

	a {
		color: $rev-green;
	}

	h1 {
		color: $rev-green;
		font-size: 1.37rem;
		margin-bottom: 0.79rem;
	}

	h2 {
		color: $rev-green;
		font-size: 1.19rem;
		padding-top: 0.59rem;
	}

	p,
	ul {
		font-size: 1rem;
	}

	ul {
		margin: 0 0 0.6rem 1.21rem;

		li {
			list-style: disc;
		}
	}
}
